import { supabase, formatResult, handleSelectQuery, handleInsertQuery, handleUpdateQuery, handleDeleteQuery } from './supabaseConnection';

// Define types to maintain compatibility with existing code
type QueryResult = {
  rows: any[];
  rowCount: number;
  error?: any;
};

interface DatabasePoolInterface {
  query: (text: string, params?: any[]) => Promise<QueryResult>;
  connect: () => Promise<any>;
  end: () => Promise<void>;
}

// Export a default object with an interface similar to the previous Pool
const db: DatabasePoolInterface = {
  // For query function, we'll need to map SQL to database functions
  query: async (sql: string, params?: any[]): Promise<QueryResult> => {
    try {
      // Basic SQL parsing to determine the type of operation
      const sqlLower = sql.toLowerCase().trim();

      if (sqlLower.startsWith('select')) {
        try {
          const result = await handleSelectQuery(sql, params);
          // Format result to match expected interface
          return formatResult(result);
        } catch (parseError: any) {
          // If parsing fails, return empty result
          return { rows: [], rowCount: 0, error: parseError };
        }
      } else if (sqlLower.startsWith('insert')) {
        const result = await handleInsertQuery(sql, params);
        return formatResult(result);
      } else if (sqlLower.startsWith('update')) {
        const result = await handleUpdateQuery(sql, params);
        return formatResult(result);
      } else if (sqlLower.startsWith('delete')) {
        const result = await handleDeleteQuery(sql, params);
        return formatResult(result);
      } else {
        // For other SQL operations, we may need to use database-specific functions
        throw new Error(`Unsupported SQL operation: ${sql.substring(0, 20)}...`);
      }
    } catch (error) {
      return {
        rows: [],
        rowCount: 0,
        error
      };
    }
  },
  connect: async () => {
    // Return an object that mimics a database client for transaction support
    const queryMethod = async (sql: string, params?: any[]): Promise<QueryResult> => {
      return await db.query(sql, params);
    };

    return {
      query: queryMethod,
      release: () => {
        // No actual connection to release
      }
    };
  },
  end: async () => {
    // Database client doesn't require explicit connection ending
    console.log('Database connection closed');
  }
};

export default db;

// Test the connection
export const connectDB = async () => {
  try {
    // Perform a simple query to test the connection
    const result = await db.query('SELECT 1 as test');
    if (result.error && result.error.code !== 'PGRST205') {
      console.error('Database connection error:', result.error);
      throw result.error;
    }
    console.log('Database connected successfully');
  } catch (error) {
    console.error('Database connection error:', error);
    throw error;
  }
};

// Admin functions for managing shipping costs
export const getShippingConfig = async (): Promise<any> => {
  try {
    const result = await db.query(
      'SELECT * FROM shipping_config ORDER BY created_at DESC LIMIT 1'
    );
    
    if (result.rows.length > 0) {
      return result.rows[0];
    } else {
      // Return default shipping configuration if none exists
      return {
        id: 1,
        min_order_amount: 50000,
        flat_rate: 1500,
        enabled: true,
        created_at: new Date().toISOString()
      };
    }
  } catch (error) {
    console.error('Error fetching shipping configuration:', error);
    // Return default configuration
    return {
      id: 1,
      min_order_amount: 50000,
      flat_rate: 1500,
      enabled: true,
      created_at: new Date().toISOString()
    };
  }
};

export const updateShippingConfig = async (config: any): Promise<any> => {
  try {
    const result = await db.query(
      'INSERT INTO shipping_config (min_order_amount, flat_rate, enabled) VALUES ($1, $2, $3) ON CONFLICT (id) DO UPDATE SET min_order_amount = $1, flat_rate = $2, enabled = $3 RETURNING *',
      [config.min_order_amount, config.flat_rate, config.enabled]
    );
    
    return result;
  } catch (error) {
    console.error('Error updating shipping configuration:', error);
    throw error;
  }
};

// Admin functions for managing tax
export const getTaxConfig = async (): Promise<any> => {
  try {
    const result = await db.query(
      'SELECT * FROM tax_config ORDER BY created_at DESC LIMIT 1'
    );
    
    if (result.rows.length > 0) {
      return result.rows[0];
    } else {
      // Return default tax configuration if none exists
      return {
        id: 1,
        rate: 0.0,
        type: 'percentage', // 'percentage' or 'fixed'
        enabled: false,
        created_at: new Date().toISOString()
      };
    }
  } catch (error) {
    console.error('Error fetching tax configuration:', error);
    // Return default configuration
    return {
      id: 1,
      rate: 0.0,
      type: 'percentage',
      enabled: false,
      created_at: new Date().toISOString()
    };
  }
};

export const updateTaxConfig = async (config: any): Promise<any> => {
  try {
    const result = await db.query(
      'INSERT INTO tax_config (rate, type, enabled) VALUES ($1, $2, $3) ON CONFLICT (id) DO UPDATE SET rate = $1, type = $2, enabled = $3 RETURNING *',
      [config.rate, config.type, config.enabled]
    );
    
    return result;
  } catch (error) {
    console.error('Error updating tax configuration:', error);
    throw error;
  }
};

// Functions to calculate shipping and tax
export const calculateShippingCost = async (subtotal: number): Promise<number> => {
  try {
    const config = await getShippingConfig();
    
    if (!config.enabled) {
      return 0; // Free shipping if disabled
    }
    
    if (subtotal >= config.min_order_amount) {
      return 0; // Free shipping for orders above threshold
    }
    
    return config.flat_rate;
  } catch (error) {
    console.error('Error calculating shipping cost:', error);
    // Fallback: return default shipping cost
    return 1500;
  }
};

export const calculateTaxAmount = async (amount: number): Promise<number> => {
  try {
    const config = await getTaxConfig();
    
    if (!config.enabled) {
      return 0; // No tax if disabled
    }
    
    if (config.type === 'fixed') {
      return config.rate; // Fixed tax amount
    } else {
      return amount * (config.rate / 100); // Percentage tax
    }
  } catch (error) {
    console.error('Error calculating tax amount:', error);
    // Fallback: return no tax
    return 0;
  }
};